
.App {
  min-height: 100%;
  flex-direction: column;
  display: flex;
  min-width: 900px;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.App-body {
  display: flex;
  flex: 1;
}

.content {
  position: relative;
  flex: 1;
}


.circular{
  position:absolute;  
  left:0; right:0;
  top:0; bottom:0;
  margin:auto;
}
.content {
  position: relative;
  flex: 1;
}

 
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* Split the screen in half */
.row:after {
  content: "";
  display: table;
  clear: both;
  position: relative; 

}

.column {
  float: center;
}

.left {
  width: 29.5%;
  margin-left: 25px;
  position: sticky; 
  top: 0;
  height: 740px;

}



.right {
  margin-right: 10px;
  width: 66.5%;
 
  float: center

}

.footer{
  margin-top: 15px;
  height: 40px;
}
.header, .menu, .content {
  box-sizing: border-box;
}

