.tooltipz {outline:none; 

}
.tooltipz strong {line-height:30px;}
.tooltipz:hover {text-decoration:none;    
} 
.tooltipz span {
    z-index:10;display:none;
   
}
.tooltipz:hover span{
    display:inline; position:absolute; 
    border:2px solid #FFF;  color:#EEE;
    background:#333 repeat-x 0 0;

}

.callout {  
 
    z-index:20;position:absolute;border:0;top:100px;left:-400px; width: 600px; height: 400px}


