.actuator-style{
    margin-top: 300px;
    width: 450px;
    height: 200px;
    }



.tooltipz-act {outline:none; 

}
.tooltipz-act strong {line-height:30px;}
.tooltipz-act:hover {text-decoration:none;    
} 
.tooltipz-act span {
    z-index:10;display:none;
   
}
.tooltipz-act:hover span{
    display:inline; position:absolute; 
    border:2px solid #FFF;  color:#EEE;
    background:#333 repeat-x 0 0;

}

.callout-act {  
 
    z-index:20;position:absolute;border:0;top:50px;left:-300px; width: 400px; height: 250px}


